import React from 'react'
import axios from 'axios';
import ErrorMessage from '../ErrorMessage';

export default function Register(props) {

    const usernameRef = React.createRef();
    const emailRef = React.createRef();
    const passwordRef = React.createRef();
    const message = React.useState("");

    const handleRegisterForm = (e) => {
        e.preventDefault();
        const requestBody = {
            username: usernameRef.current.value,
            email: emailRef.current.value,
            password: passwordRef.current.value
        }
        console.log(requestBody);
        axios.post(
            `${process.env.REACT_APP_BASE_BACKEND_URL}/register`,
            requestBody,
            {
                headers: {
                    'Content-Type': 'application/json'
                },
                withCredentials: true
            }
        ).then((res) => {
            console.log(res);
            if (res.data.body?.errorMessage) {
                console.error(res.data.body.errorMessage);
            } else {
                props.history.push('/login');
            }

        })
    }

    return (
        <div className="container flex-container">
            <h1>Register</h1>
            <form onSubmit={handleRegisterForm} className="flex-container form-container">
                <input type="email" className="form-control-input" ref={emailRef} placeholder={"Email"} />
                <input type="text" className="form-control-input" ref={usernameRef} placeholder={"Username"} />
                <input type="password" className="form-control-input" ref={passwordRef} placeholder={"Password"} />
                <button type="submit" className="button">Submit</button>
            </form>
            <ErrorMessage message={message} />
        </div>
    )
}
