import axios from 'axios';
import React, { Component } from 'react'
import ErrorMessage from '../ErrorMessage'

export default class Login extends Component {

    constructor(props) {
        super(props);
        this.state = {
            errorMessage: ''
        }
        this.handleSubmitForm = this.handleSubmitForm.bind(this);
        this.removeErrorClass = this.removeErrorClass.bind(this);
        this.setErrorValidation = this.setErrorValidation.bind(this);
        this.usernameRef = React.createRef(null);
        this.passwordRef = React.createRef(null);
    }


    setErrorValidation(msg) {
        this.usernameRef.current.classList.add('form-control-input-error');
        this.passwordRef.current.classList.add('form-control-input-error');
        this.setState({ errorMessage: msg })
    }

    handleSubmitForm(e) {
        e.preventDefault();
        if (this.usernameRef.current.value !== "" && this.passwordRef.current.value !== "") {
            const requestBody = {
                username: this.usernameRef.current.value,
                password: this.passwordRef.current.value
            }
            axios.post(
                `${process.env.REACT_APP_BASE_BACKEND_URL}/login`,
                requestBody,
                {
                    headers: {
                        'Content-Type': 'application/json'
                    },
                    withCredentials: true
                }
            ).then((res) => {
                console.log(res.data);
                if (res.data.status === 200 && !res.data.body?.errorMessage) {
                    this.props.history.push('/');
                } else {
                    this.setErrorValidation(res.data.body.errorMessage);
                }
            })
        } else {
            this.setErrorValidation('Please validate fields before submit!');
        }

    }

    removeErrorClass() {
        if (this.usernameRef.current.className.includes('form-control-input-error')) {
            this.usernameRef.current.classList.remove('form-control-input-error');
            this.passwordRef.current.classList.remove('form-control-input-error');
            this.setState({ errorMessage: '' });
        }
    }

    render() {
        return (
            <div className="container flex-container form-container">
                <h1 className="title">Login</h1>
                <ErrorMessage message={this.state.errorMessage} />
                <form method="POST" action="/" onSubmit={this.handleSubmitForm} className="flex-container form-container">
                    <input type="text" className="form-control-input" ref={this.usernameRef} placeholder="Username" onFocus={this.removeErrorClass} />
                    <input type="password" className="form-control-input" ref={this.passwordRef} placeholder="Password" onChange={this.removeErrorClass} />
                    <button type="submit" className="button">Submit</button>
                </form>
            </div>
        )
    }
}
