import React from 'react'

export default function useMobileResolution() {
    const [
        isMobile,
        setIsMobile
    ] = React.useState(false);

    const detectIfMobileWidth = (obj) => {
        return obj.innerWidth < 768;
    }

    const setMobileResolution = (e) => {
        setIsMobile(detectIfMobileWidth(e));
    }

    React.useEffect(
        () => {
            setMobileResolution(window);


            window.addEventListener(
                'resize',
                (e) => setMobileResolution(e.target)
            )


            return window.removeEventListener(
                'resize',
                setMobileResolution
            )
        },
        []
    );

    return isMobile;
}