import React from 'react'
import GoBackButton from '../components/GoBackButton';
import Book from './Book';
import { checkUserAuthenticated } from '../lib/auth';
import axios from 'axios';
import Loader from '../components/Loader';

export default class SavedBooks extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            savedBooks: [],
            loading: true
        }
    }

    componentDidMount() {
        checkUserAuthenticated(() => this.props.history.push('/login'));
        axios(
            `${process.env.REACT_APP_BASE_BACKEND_URL}/api/books/saved`,
            {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                withCredentials: true
            }
        ).then((response) => {
            if (response.data.status === 200 && !response.data.body?.errorMessage && response.data.body?.books) {
                console.log(response.data.body.books);
                this.setState({
                    loading: false,
                    savedBooks: response.data.body.books
                });
            }
        })
    }

    render() {
        return (
            <div className="container">
                <GoBackButton destination={'/'} />
                <article className="container">
                    <header>
                        <h1>Libri salvati</h1>
                    </header>
                    {this.state.loading && <Loader />}
                    {
                        !this.state.loading &&
                        <section className="container">
                            {this.state.savedBooks.length === 0 && <h2>Nessun libro salvato</h2>}
                            {
                                this.state.savedBooks.map((book) => (
                                    <article key={book.book_id} className="relative-parent">
                                        <Book key={book.book_id} id={book.book_id} drawGoBack={false} drawDeleteButton={true} />
                                    </article>
                                ))
                            }
                        </section>
                    }
                </article>
            </div>
        )
    }
}