import React, { useState, useEffect } from 'react'
import Loader from '../Loader'
import PropTypes from 'prop-types'
import BookDetailed from './BookDetailed';
import Pricing from '../Pricing'
import useMobileResolution from '../useMobileResolution'
import axios from 'axios';
import ErrorMessage from '../ErrorMessage';

export default function BookContainerBody(props) {

    const [
        isLoading,
        setIsLoading
    ] = useState(true);
    const [
        book,
        setBook
    ] = useState({});

    const [
        error,
        setErrore
    ] = useState({
        isError: false,
        msg: ''
    });


    const isMobile = useMobileResolution();

    useEffect(
        () => {
            setIsLoading(true);
            axios(`${process.env.REACT_APP_GOOGLE_BOOKS_API}/${props.bookId}`).then((data) => {

                /*
                 * console.log(
                 *     'single book request ',
                 *     data.data
                 * );
                 */
                // throw (new Error("Errore nel caricamento"));


                setBook(data.data)
                setIsLoading(false);

            }).
                catch(() => {
                    setErrore({
                        isError: true,
                        msg: 'Errore durante il caricamento, riprovare'
                    });
                }).
                finally(() => {
                    setIsLoading(false)
                })
        },
        []
    )

    if (book === {} || isLoading) {
        return <Loader />
    }

    return (
        <section className={`container book ${isMobile
            ? 'book-md'
            : 'book-lg'}
            ${error.isError
                ? 'book-error'
                : ''}`}>
            {error.isError && <ErrorMessage message={error.msg} />}
            {book?.volumeInfo && <BookDetailed book={book} drawDeleteButton={props.drawDeleteButton} />}
            {book?.saleInfo && <Pricing saleInfo={book.saleInfo} />}
        </section>
    )
}

BookContainerBody.propTypes = {
    bookId: PropTypes.string.isRequired,
    drawDeleteButton: PropTypes.bool.isRequired
}
