import axios from "axios"

export const cbAuthenticationFailure = (res, cb) => {
    if (res.data.body?.errorMessage) {
        cb();
    }
}

export const checkUserAuthenticated = (cb) => {
    axios(
        `${process.env.REACT_APP_BASE_BACKEND_URL}/authenticated`,
        {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            withCredentials: true
        }
    ).then((res) => cbAuthenticationFailure(
        res,
        cb
    ))
}